<template>
  <v-app>
    <v-app-bar app color="primary" dark style="font-size:25px;">
      CHSLI PPE Hub Staffing Schedule
      <v-spacer></v-spacer>
      <v-col cols="3">
        <v-text-field
          v-if="displayField"
          v-model="enteredText"
          label="Password"
          filled
          style="height:56px;"
        ></v-text-field>
      </v-col>
      <v-col cols="1">
        <v-btn v-if="displayField" small color="grey" @click="login">Login</v-btn>
      </v-col>
      <v-btn right small color="error" @click="login">{{ buttonText }}</v-btn>
    </v-app-bar>
    <v-content>
      <Scheduling ref="scheduling" />
    </v-content>
  </v-app>
</template>

<script>
import Scheduling from "./components/Scheduling";
const matchText = "Mercy@2020";

export default {
  name: "App",

  components: {
    Scheduling
  },

  data: () => ({
    //
    displayField: false,
    buttonText: "Admin Use Only",
    enteredText: ""
  }),
  methods: {
    toggleField() {
      this.displayField = !this.displayField;
    },
    login() {
      // very basic
      if (this.buttonText === "Admin Use Only") {
        if (!this.displayField) {
          this.toggleField();
        } else {
          if (this.enteredText == matchText) {
            alert("Logged In");
            this.$refs.scheduling.isAdmin = true;
            this.toggleField();
            this.buttonText = "Logout";
          } else {
            alert("Incorrect Password");
          }
        }
        this.enteredText = "";
      } else {
        this.$refs.scheduling.isAdmin = false;
        this.buttonText = "Admin Use Only";
      }
    }
  }
};
</script>
